import Operate from "@/mixs/Operate";

const adminMessage = {
    data() {
        return {
            //
            SAVE_OK: 20001,
            DELETE_OK: 20002,
            UPDATE_OK: 20003,
            GET_OK: 20004,
            PASSWORD_OK: 20005,
            PASSWORD_OK_ADMIN: 20006,
            //
            USER_OK: 20007,
            //
            Cancel_OK: 20008,
            REGISTER_OK: 20009,
            PAGE_OK: 20004,
            //
            SAVE_ERR: 10001,
            DELETE_ERR: 10002,
            UPDATE_ERR: 10003,
            GET_ERR: 10004,
            PASSWORD_ERR: 10005,
            //
            USER_ERR: 10006,

            //
            SYSTEM_ERR: 10001,
            SYSTEM_TIMEOUT_ERR: 10002,
            SYSTEM_UNKNOW_ERR: 19999,
            BUSINESS_ERR: 11002,

            //
            UPLOAD_OK: 2005,
            UPLOAD_ERR: 10005,
        }
    },
    methods: {

        cancelLoginOk(this_) {
            this_.$message({
                type:"success",
                message:""
            })
        },
        userErr(this_) {
            this_.$message({
                type: 'error',
                message: ''
            });
        },
        loginOk(this_, ts) {
            this_.$message({
                type: "success",
                message: `${ts}`
            })
        },
        passwordErr1(this_) {
            this_.$message({
                type: "error",
                message: "~"
            })
        },
        passwordErr2(this_) {
            this_.$message({
                type: 'error',
                message: ' '
            });
        },
        formDataErr(this_) {
            this_.$message({
                type: 'error',
                message: ' '
            });
        },

        registerErr(this_) {
            this_.$message({
                type: "error",
                message: ``
            })
        },
        registerOK(this_, ms) {
            this_.$message({
                type: "success",
                message: `${ms},`
            })
        },
        notNull(_this, mes) {
            _this.$message({
                type: 'error',
                message: mes + ''
            });
        },
        internetErr(_this) {
            _this.$message({
                type: 'error',
                message: ''
            });
        },
        ipErr(_this) {
            _this.$message({
                type: 'error',
                message: 'ip'
            });
        },
        emailErr(_this) {
            _this.$message({
                type: 'error',
                message: ''
            });
        },
        IllegalSearch(this_) {
            this_.$message({
                type: 'warning',
                message: ''
            });
        },
        IllegalDelete(this_) {
            this_.$message({
                type: 'warning',
                message: ''
            });
        },
        AddCommentOk(this_) {
            this_.$message({
                type: "success",
                message: ""
            })
        }, AddCommentErr(this_) {
            this_.$message({
                type: "error",
                message: ""
            })
        },
        AddOk(this_) {
            this_.$message({
                type: "success",
                message: ""
            })
        }, AddErr(this_, errms) {
            this_.$message({
                type: "error",
                message: "" + errms
            })
        }, updateOk(this_) {
            this_.$message({
                type: "success",
                message: ""
            })
        },
        updateErr(this_) {
            this_.$message({
                type: "error",
                message: ""
            })
        },
        deleteCancel(this_) {
            this_.$message({
                type: 'info',
                message: ''
            });
        },
        deleteOk(this_) {
            this_.$message({
                type: 'success',
                message: ''
            });
        },
        deleteErr(this_) {
            this_.$message({
                type: 'error',
                message: ''
            });
        },
        deleteMs(resp,) {
            if (resp.data.code === this.DELETE_OK) {

                this.$message({
                    type: 'success',
                    message: ''
                });

            } else {
                this.$message({
                    type: 'error',
                    message: ''
                });
            }
        },
        showStatusMessage(resp, Op) {
            const code = Operate.data()
            switch (Op) {
                case code.ADD:
                    break
                case code.DELETE:
                    this.deleteMs(resp, Op)
                    break
                case code.UPDATE:
                    break
                case code.SEARCH:
                    break
            }
        },


    }
}
export default adminMessage
