<template>
  <div class="pageDataCls">
    <div>
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="search.content" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="search.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="search.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <div style="display: flex;">
      <el-button type="danger" @click="manyRemoveData" plain></el-button>
    </div>
    <template>
      <el-table
          ref="clearCheckBox"
          :data="tableData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
          @selection-change="handleSelectionChange"
      >

        <el-table-column
            type="selection"
            width="50">
        </el-table-column>
        <el-table-column
            label=""
            type="index"
            width="48">
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentId"
            label="ID">
        </el-table-column>
        <el-table-column
            align="center"
            prop="articleTitle"
            label="">
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentAuthorName"
            label="">
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentAuthorEmail"
            label="">
        </el-table-column>

        <el-table-column
            align="center"
            prop="commentContent"
            label="">
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentIp"
            label="IP">
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentCreateTime"
            label=""
            sortable
        >
        </el-table-column>
        <el-table-column
            align="center"
            prop="commentRole"
            label="">
        </el-table-column>
        <el-table-column
            align="center"
            prop="operation"
              label=""   fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="editData(scope.row)"></el-button>
            <br>
            <br>
            <el-button type="danger" size="small" @click="removeData(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <!---->
    <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5, 10,15,20]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog
        class="el-dialog11"
        :fullscreen="true"
        :modal="false"
        title=""
        :visible.sync="dialogVisibleUpdate"
        width="30%"
        :before-close="handleClose">
      <el-form label-width="80px" :model="updateForm">
        <el-form-item label="">
          <el-input v-model="updateForm.commentAuthorName"></el-input>
        </el-form-item>
        <el-form-item label="ip">
          <el-input v-model="updateForm.commentIp"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="updateForm.commentAuthorEmail"></el-input>
        </el-form-item>
        <el-form-item label="">
          <VueEidtor
              ref="Quill" style="" class="quill-editor" v-model="updateForm.commentContent"></VueEidtor>
        </el-form-item>
        <el-table-column
            style="display: flex;    flex-direction: row;
    justify-content: space-evenly;"
             label=""   fixed="right">
          <el-button type="primary" @click="UpadetCli"></el-button>
          <br>
          <el-button type="danger" @click="updateCancel"></el-button>
        </el-table-column>

      </el-form>
    </el-dialog>
  </div>


</template>
<script>

import {crud_D, crud_R, crud_U} from "@/mixs/CRUD";
import {quillEditor} from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import showAdminMessage from "@/mixs/ShowAdminMessage";
import {ApiOrigin} from "@/config/ApiOrigin";
import {emailRegx} from "@/utils/Regx";

export default {
  name: "CommentFact",
  components: {
    VueEidtor: quillEditor
  },
  mounted() {
    this.selectAll()

  },
  methods: {
    updateCancel() {
      this.dialogVisibleUpdate = false;
      this.QingKongCli();
    },
    ZanCunCli() {
    },
    QingKongCli() {
      this.updateForm = {}
    },
    //

    //
    UpadetCli() {
      //
      console.log(this.updateForm)
      console.log(emailRegx(this.updateForm.commentAuthorEmail))
       if (emailRegx(this.updateForm.commentAuthorEmail)) {
         crud_U.updateData(this, this.updateForm, ApiOrigin+"/Admin/updateComment")
         this.dialogVisibleUpdate = false;
         this.QingKongCli()
       }
      // //
       else {
        showAdminMessage.methods.emailErr(this)
       }
    },


    //
    handleSizeChange(val) {

      this.pageSize = val;
      this.selectAll();
    },
    //
    handleCurrentChange(val) {
      this.currentPage = val;
      this.selectAll();
    },
    //

    //
    /**
     * 
     */
    selectAll() {
      var data = {
        currPage: this.currentPage,
        pageSize: this.pageSize,
        search: this.search
      }
      crud_R.selectPage(this, data, ApiOrigin+"/Admin/getCommentPage")
    },


    //
    onSubmit() {
      this.selectAll();
    },
    handleClose() {
      this.dialogVisible = false;
      this.dialogVisibleUpdate = false;
    },

    tableRowClassName({row, rowIndex}) {
      if (rowIndex === 1) {
        return 'warning-row';
      } else if (rowIndex === 3) {
        return 'success-row';
      }
      return '';
    },
    //  scope.row
    editData(row) {
      //

      this.dialogVisibleUpdate = true;
      this.updateForm = row
    },
    //
    removeData(row) {
      var arrIDs = [row.commentId];
      crud_D.removeData(this, arrIDs, ApiOrigin+"/Admin/removeComment")
    },

    //check
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.clearCheckBox.toggleRowSelection(row);
        });
      } else {
        this.$refs.clearCheckBox.clearSelection();
      }
    },

    //
    manyRemoveData() {
      let selectedIds = []
      for (let i = 0; i <this.multipleSelection.length ; i++) {
        selectedIds.push(this.multipleSelection[i].commentId)
      }
      if (selectedIds.length > 0) {
        crud_D.removeData(this, selectedIds, ApiOrigin+"/Admin/removeComment")
      } else {
        showAdminMessage.methods.IllegalDelete(this)
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

  },
  data() {
    return {
      //
      updateForm: {},
      totalCount: 0,
      checkedDatas: [],
      total: 400,
      currentPage: 1,
      pageSize: 5,
      dialogVisibleUpdate: false,
      tableData: [],
      selectedIds: [],
      multipleSelection: [],
      search: {
        title: "",
        content: "",
        email: "",
        name: "",
      },

    }
  }
}
</script>

<style scoped>
td {
  font-size: 10px;
}

.pageDataCls {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: aliceblue;
}

.pageDataCls > div:first-child {
  display: flex;
  margin: 13px;
}

.quill-editor {
  width: 1200px;
  min-width: 600px;
  min-height: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
}

.el-dialog.is-fullscreen {
  background-color: #2c3e50;
}
</style>
