//     +    URL +                                 %2B
//  URL+           %20
// /                                        %2F
//     ?    URL                             %3F
//     %                                              %25
// #                                                      %23
// &    URL                   %26
//     =    URL                                 %3D
export default function CharsetFilter(str) {

    if (str != null) {
        if (str.indexOf('%') >= 1){
            str = str.replace("%", "rtfgv")
        }
        if (str.indexOf('#') >= 1) {
            str = str.replace("#", "%23")
        }
        if (str.indexOf('+') >= 1) {
            str = str.replace("+", "%2B")
        }
        if (str.indexOf('?') >= 1) {
            str = str.replace("?", "%2F")
        }
        if (str.indexOf('&') >= 1) {
            str = str.replace("&", "%26")
        }
        if (str.indexOf('=') >= 1) {
            str = str.replace("=", "%3D")
        }

    }
    return str
}
